<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='jiaowurenyuan'"  label='' prop="jiaowurenyuanUuidNumber">
               <el-input v-model="ruleForm.jiaowurenyuanUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='jiaowurenyuan'"  label='' prop="jiaowurenyuanName">
               <el-input v-model="ruleForm.jiaowurenyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='jiaowurenyuan'"  label='' prop="jiaowurenyuanIdNumber">
               <el-input v-model="ruleForm.jiaowurenyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuUuidNumber">
               <el-input v-model="ruleForm.yonghuUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuName">
               <el-input v-model="ruleForm.yonghuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuAddress">
               <el-input v-model="ruleForm.yonghuAddress"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="xueyuanTypes">
                 <el-select v-model="ruleForm.xueyuanTypes"  placeholder=''>
                     <el-option
                             v-for="(item,index) in xueyuanTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="banjiTypes">
                 <el-select v-model="ruleForm.banjiTypes"  placeholder=''>
                     <el-option
                             v-for="(item,index) in banjiTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="xuejiTypes">
                 <el-select v-model="ruleForm.xuejiTypes"  placeholder=''>
                     <el-option
                             v-for="(item,index) in xuejiTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
        // 
        xueyuanTypesOptions : [],
        // 
        banjiTypesOptions : [],
        // 
        xuejiTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=xueyuan_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.xueyuanTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=banji_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.banjiTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=xueji_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.xuejiTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },


    onUpdateHandler() {
                         if((!this.ruleForm.jiaowurenyuanUuidNumber)&& 'jiaowurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.jiaowurenyuanName)&& 'jiaowurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.jiaowurenyuanIdNumber)&& 'jiaowurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuUuidNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuName)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuAddress)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xueyuanTypes)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.banjiTypes)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xuejiTypes)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

        if((!this.ruleForm.sexTypes) && this.flag!='users'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
